        )
        .then(function(res) {
          that.value1 = res.data.data.phones[0].phone;
          that.value2 = res.data.data.phones[1].phone;
          that.value3 = res.data.data.phones[2].phone;
          that.value4 = res.data.data.phones[3].phone;
